@import url('https://fonts.googleapis.com/css2?family=Butler:wght@400;500;600;700&display=swap');

:root {
  --foreground-rgb: 0, 0, 0;
  --background-rgb: 255, 255, 255;
  --font-butler: 'Playfair Display', serif;
